/* foro.css — Diseño renovado SIN Times New Roman */

/* ---------- Reset / Base ---------- */
* { box-sizing: border-box; }
:root { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

body {
  margin: 0;
  font-family: "Inter", "Segoe UI", sans-serif;
  background: linear-gradient(180deg, #f7f9fc 0%, #ffffff 100%);
  color: #0a1424;
}

/* ---------- Quitar Times en TODO ---------- */
body, h1, h2, h3, p, input, textarea, button, small, strong, div, span {
  font-family: "Inter", "Segoe UI", sans-serif !important;
}

/* ---------- HEADER (fijo) ---------- */
.foro-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 74px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 22px;
  background: linear-gradient(90deg, #061a33, #0d2e59);
  color: #fff;
  box-shadow: 0 6px 20px rgba(10, 25, 50, 0.25);
  z-index: 1200;
}

.foro-header h1 {
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  margin: 0;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
}

.foro-header-buttons {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Botones del header */
.header-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 8px 14px;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  border: 1px solid transparent;
  transition: transform .12s ease, box-shadow .12s ease, background .12s ease;
}

/* Nuevo debate */
.header-btn.new {
  background: linear-gradient(180deg, #2b4d8a, #18335a);
  color: #fff;
  box-shadow: 0 4px 10px rgba(25, 60, 110, 0.25);
}

.header-btn.new:hover {
  transform: translateY(-3px);
  background: linear-gradient(180deg, #325da3, #1e3f6e);
}

/* Regresar */
.header-btn.back {
  background: rgba(255, 255, 255, 0.08);
  color: #f5f7fa;
  border: 1px solid rgba(255, 255, 255, 0.12);
}

.header-btn.back:hover {
  background: rgba(255, 255, 255, 0.16);
}

/* ---------- CONTENEDOR PRINCIPAL ---------- */
.foro-container { padding-top: 90px; }

.foro-main {
  max-width: 940px;
  margin: 32px auto;
  padding: 28px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(9, 20, 38, 0.08);
}

/* ---------- LISTA DE DEBATES ---------- */
.debate-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 14px;
  grid-template-columns: 1fr;
}

.debate-item {
  padding: 20px;
  border-radius: 10px;
  background: linear-gradient(180deg, #fbfdff, #f5f8fb);
  border: 1px solid #dde5f2;
  transition: transform .12s ease, box-shadow .12s ease;
  cursor: pointer;
}

.debate-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 24px rgba(14, 38, 72, 0.08);
}

.debate-item h3 {
  font-size: 1.25rem;
  margin: 0 0 6px;
  color: #0d274a;
  font-weight: bold;
}

.debate-item p {
  color: #344b63;
  line-height: 1.55;
  margin: 0;
  font-size: 1rem;
}

.debate-item small {
  display: block;
  margin-top: 8px;
  color: #71839c;
  font-size: 0.9rem;
  font-style: italic;
}

/* ---------- FORMULARIO NUEVO DEBATE ---------- */
.new-debate {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.new-debate input,
.new-debate textarea {
  width: 100%;
  padding: 12px 14px;
  border-radius: 8px;
  border: 1px solid #cfd9e8;
  background: #fafcfe;
  font-size: 1rem;
  color: #0b223d;
  transition: all .2s ease;
}

.new-debate textarea {
  min-height: 140px;
  resize: vertical;
}

.new-debate input:focus,
.new-debate textarea:focus {
  outline: none;
  border-color: #2b4d8a;
  box-shadow: 0 0 8px rgba(43, 77, 138, 0.2);
}

/* ---------- BOTONES ---------- */
.send-btn {
  padding: 10px 16px;
  border-radius: 8px;
  background: linear-gradient(180deg, #2b4d8a, #18335a);
  color: #fff;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: transform .12s ease, box-shadow .12s ease;
}

.send-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(27, 57, 110, 0.16);
}

/* ---------- COMENTARIOS ---------- */
.comentarios {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.comentario {
  padding: 14px 16px;
  border-radius: 10px;
  background: #f9fbfd;
  border: 1px solid #e3eaf4;
  color: #0f2135;
}

.comentario strong {
  color: #102b52;
  font-weight: bold;
}

.comentario small {
  color: #7a8ea8;
  margin-left: 8px;
  font-size: 0.85rem;
  font-style: italic;
}

/* ---------- TEXTAREA comentario ---------- */
.comentario-input {
  width: 100%;
  min-height: 120px;
  padding: 1rem 1.2rem;
  border: 2px solid #ccd6e0;
  border-radius: 12px;
  background-color: #f9fbfd;
  color: #1a2a44;
  font-size: 1rem;
  line-height: 1.5;
  resize: vertical;
  transition: all 0.25s ease;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.05);
  margin: 1.2rem 0;
}

.comentario-input::placeholder {
  color: #8a9bb5;
  font-style: italic;
}

.comentario-input:hover {
  border-color: #90b4e0;
  background-color: #f1f6fb;
}

.comentario-input:focus {
  outline: none;
  border-color: #2b6cb0;
  background-color: #ffffff;
  box-shadow: 0 0 6px rgba(43, 108, 176, 0.3);
}

/* ---------- Botones comentarios ---------- */
.comentario-buttons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 10px;
}

.back-btn {
  padding: 9px 14px;
  border-radius: 8px;
  border: 1px solid #a5b8d1;
  background: transparent;
  color: #0f2135;
  font-weight: bold;
  cursor: pointer;
  transition: all .15s ease;
}

.back-btn.inline {
  border-color: #2b4d8a;
  color: #16334f;
}

.back-btn:hover {
  background: #f2f6fb;
}

/* ---------- TÍTULOS INTERNOS ---------- */
h2, h3 {
  color: #0d274a;
  margin-bottom: 10px;
}

h2 {
  font-size: 1.4rem;
  border-bottom: 2px solid #2b4d8a;
  padding-bottom: 6px;
  display: block;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

h3 {
  font-size: 1.15rem;
  color: #1a3b6e;
  font-style: italic;
}

/* ---------- Responsivo general ---------- */
@media (max-width: 640px) {
  .foro-header {
    padding: 10px;
    height: auto;
    flex-wrap: wrap;
    gap: 6px;
  }

  .foro-header h1 { font-size: 1.2rem; }
  .foro-main { padding: 18px; margin: 12px; }

  .comentario-buttons {
    flex-direction: column-reverse;
  }

  .comentario-buttons button {
    width: 100%;
  }
}

/* ----- Fix títulos en celulares ----- */
@media (max-width: 480px) {
  h1, h2, h3 {
    font-size: 1.1rem !important;
    word-wrap: break-word;
    overflow-wrap: break-word;
    white-space: normal !important;
  }

  .foro-header h1 {
    font-size: 1.1rem !important;
  }
}

/* ----- Fix: debate-item NO se sale en celular ----- */
@media (max-width: 480px) {

  .debate-list {
    padding: 0 8px !important;
    grid-template-columns: 1fr !important;
  }

  .debate-item {
    width: 100% !important;
    max-width: 100% !important;
    padding: 14px !important;
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
    white-space: normal !important;
  }

  .debate-item h3,
  .debate-item p {
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
    white-space: normal !important;
  }
}
